import {Component, EventEmitter, Input, OnInit, Output} from '@angular/core';
import {NgbActiveModal} from '@ng-bootstrap/ng-bootstrap';
import {ToastrService} from 'ngx-toastr';
import {ServicesService} from '../../services/services.service';
import {Limits} from '../../tokens/token';
import {FormControl, FormGroup, Validators} from '@angular/forms';
import {Constants} from '../../../../constants';
import {UserService} from '../../../../auth/service';
import {limitLabels, unlimited} from '../../../utils/limits';

@Component({
    selector: 'app-user-limits',
    templateUrl: './user-limits.component.html',
    styleUrls: ['./user-limits.component.scss']
})
export class UserLimitsComponent implements OnInit {
    constructor(public activeModal: NgbActiveModal, private toastr: ToastrService,
                private users: UserService
    ) {
    }

    @Input() limits: Limits;
    @Input() userId: number;
    @Output() newLimits = new EventEmitter<Partial<Limits>>();
    form = new FormGroup({
        'limit_daily': new FormControl(null, [Validators.required, Validators.min(-1)]),
        'limit_weekly': new FormControl(null, [Validators.required, Validators.min(-1)]),
        'limit_bi_weekly': new FormControl(null, [Validators.required, Validators.min(-1)]),
        'limit_monthly': new FormControl(null, [Validators.required, Validators.min(-1)])
    });
    submitted = false;
    loading = true;
    loadingSave = false;
    group = 'service';
    labels = limitLabels;
    selectedLimit = false as any;

    ngOnInit() {
        this.form.disable();
        this.users.byId(this.userId).then(res => {
            this.limits = res.login_limit;
            this.form.setValue(this.limits);
            this.selectedLimit = Object.keys(this.limits).find(k => this.limits[k] !== -1) || false;
            this.form.enable();
        }).catch(err => {
            this.toastr.error(Constants.error);
        }).finally(() => {
            this.loading = false;
        });
    }

    setLimit(key) {
        if (this.labels[key]) {
            let c: FormControl = this.form.controls[key];
            if (c.value === -1) {
                c.setValue(null);
            }
        }
    }

    save() {
        let l = this.form.value[this.selectedLimit];
        this.form.setValue(unlimited);
        if (this.selectedLimit) {
            this.form.controls[this.selectedLimit].setValue(l);
        }
        this.submitted = true;
        this.loadingSave = true;
        if (this.form.invalid) {
            return;
        }
        this.users.updateLimits(this.userId, this.form.value).then(res => {
            this.toastr.success('Updated User Limits successfully ðŸŽ‰');
            this.newLimits.emit(this.form.value);
            this.activeModal.dismiss();
        }).catch(err => {
            this.toastr.error(Constants.error);
        }).finally(() => {
            this.loadingSave = false;
        });
    }
}
