import {Component, OnInit, ViewEncapsulation, Input} from '@angular/core';
import {FormControl, FormGroup, Validators} from '@angular/forms';

import {NgbActiveModal} from '@ng-bootstrap/ng-bootstrap';
import {ToastrService} from 'ngx-toastr';
import { User1} from '../../../../auth/models';
import {RolesService} from '../../roles/roles.service';
import {UserService} from '../../../../auth/service';

@Component({
    selector: 'app-user-edit',
    templateUrl: './user-edit.component.html',
    styleUrls: ['./user-edit.component.scss'],
    encapsulation: ViewEncapsulation.None
})
export class UserEditComponent implements OnInit {
    constructor(public activeModal: NgbActiveModal, private toastr: ToastrService,
                private role: RolesService,
                private user: UserService
    ) {
    }

    @Input() oldUser: User1;

    selectedRoles: string[] = [];

    roles: { custom_role_id: number, custom_role_name: string }[];
    loading = false;
    form = new FormGroup({
        'first_name': new FormControl(null, [Validators.required, Validators.minLength(3)]),
        'last_name': new FormControl(null, [Validators.required, Validators.minLength(3)]),
        'email': new FormControl(null, [Validators.required, Validators.email]),
    });
    taken = false;
    submitted = false;

    ngOnInit() {
        this.role.getList().then(res => {
            this.roles = res.data;
            let rolesbyName = {};
            this.roles.forEach(r => rolesbyName[r.custom_role_name] = r.custom_role_id);
            this.selectedRoles = this.oldUser.roles.map(r => rolesbyName[r]);
            console.log(this.selectedRoles);
        });
        this.form.setValue({
            email: this.oldUser.email,
            first_name: this.oldUser.first_name,
            last_name: this.oldUser.last_name,
        });
    }

    save() {
        this.submitted = true;
        this.taken = false;
        if (this.form.invalid || (this.selectedRoles.length === 0)) {
            return;
        }
        let o = {...this.form.value, role_ids: this.selectedRoles};
        this.loading = true;
        this.user.update(this.oldUser.id, o).then(res => {
            if (res?.success) {
                this.toastr.success('User Created Successfully ðŸŽ‰');
                this.activeModal.dismiss();
                return;
            }
            this.toastr.error('Something went wrong.');
        }).catch(err => {
            console.log('err: ', err?.error?.error);
            if (err?.error?.error === 'unique-email') {
                this.toastr.error('Email already registered.');
                this.taken = true;
            } else {
                this.toastr.error('Something went wrong.');
            }
        }).finally(() => {
            this.loading = false;
        });
    }
}
